@charset "utf-8";
* {
    margin: 0;
    padding: 0;
}

body{
    font-family: '微软雅黑';
    overflow-x: hidden;
    margin: auto;
}
/*去除搜索框中的叉*/
input::-webkit-search-cancel-button{
    display: none;
}
/*去除number类型的上下箭头*/
input[type=number] {  
    -moz-appearance:textfield;  
}  
input[type=number]::-webkit-inner-spin-button,  
input[type=number]::-webkit-outer-spin-button {  
    -webkit-appearance: none;  
    margin: 0;  
} 
/*webkit去除表单元素的默认样式*/
input,
textarea,
button {
    appearance: none;
    -webkit-appearance: none;
    outline: none;
    border:1px solid #aaa;
}
/*webkit表单输入框placeholder的颜色值*/
input::-webkit-input-placeholder{color: #ddd;}
input:focus::-webkit-input-placeholder{color: #eee;}
/*去除android a/button/input标签被点击时产生的边框 去除ios a标签被点击时产生的半透明灰色背景*/
a,
button,
input{
    -webkit-tap-highlight-color:rgba(255,0,0,0);
}
ul,ol{
    list-style-type: none;
}
img{
    // width: 100%;
}
.centerV:before{
    content: "";
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    font-size: 0;
}
.justifyLayout:after{
    content: "";
    width: 100%;
    display: inline-block;
}
a{
    text-decoration: none;
    color:black;
}
.shadow{
    overflow: hidden;
    background-color: #ECEFF4;
}
#app{
    height: 100vh;
    display: flex;
    flex-direction: column;
}
.flex_layout{
    display: flex;
    flex: 1;
}
#router_warp{
    flex: 1;
    overflow-y: auto;
    padding: 10px;
}
button{
    padding:1px 10px;
    text-align: center;
    border-radius: 3px;
    height: 28px;
    cursor: pointer;
    font-size: 14px;
    border: none;
    color: #fff;

}
input{

    border-radius: 6px;
    font-size: 14px;
    height: 30px;
    border:1px solid #D4D4DC;
    
}
input::-ms-input-placeholder{
    color:#919191;
    text-indent:0.5em;
    }
input::-webkit-input-placeholder{
    text-indent: 0.5em;
    color:#919191;
}

button:active {
    background-color: #8B93A0;
}
table{
    text-align:center;
}
table tr:nth-child(1){
    font-weight: 600;
}
table tr:hover{
    background-color: #F5F5F5;
}
.fin-change{
    background-color:#F64B30; 
}
.fin-done{
    background-color:#52C436;
     width: 32%;
}
.fin-delete{
    background-color: #D9534F;
    border: solid 1px #D9534F;
    color: #fff;
    width: 32%;
    margin: 0;
}
.all-one{
    background-color: #aaa;

}
/*input[type="file"]{
    height: 100%;
}*/


.fin-search{
    background-color: #fff;
    color: #398DEE;
    border: solid 1px #398DEE;
    font-size: 13px;
}
.fin-all-show{
    background-color: #fff;
    color: #398DEE;
    border: solid 1px #398DEE;
    font-size: 13px;
}
.fin-search:hover,.fin-all-show:hover{
    background-color: #398DEE !important;
    color: #fff;
}
.fin-delete:hover{
    background-color: #C9302C;
}
.fin-write{
    background-color: #F64B30;
    width: 32%;
    margin:0 5px;
}
.fin-write:hover{
    background-color: #F53E27;
}
.alert-close{
    position:absolute;
    right:0; 
    font-size: 25px;
    background-color: rgba(0,0,0,.5);
    color: #fff;
}


